@import "../../../global";

.box {
  display: block;
  width: 100%;
  height: auto;
  background-color: rgba(255,255,255,1);

  .name {
    display: block;
    width: 100%;
    height: auto;
    padding: px_to_rem(60) 0;
    span {
      display: block;
      height: px_to_rem(34);
      line-height: px_to_rem(34);
      font-size: px_to_rem(36);
      font-family: PingFang SC;
      font-weight: 500;
      color: rgba(51,51,51,1);
      text-align: center; } }

  .listBox {
    display: block;
    margin-bottom: px_to_rem(50);

    .list {
      display: block;
      margin: 0 px_to_rem(30);
      width: calc(100% - #{px_to_rem(30)} * 2);
      height: auto;
      overflow: hidden;
      li {
        display: block;
        width: calc((100% - 1px * 6 - #{px_to_rem(15)} * 2) / 3);
        height: px_to_rem(218);
        float: left;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: px_to_rem(5);
        a {
          display: block;
          width: calc(100% - #{px_to_rem(21)} * 2);
          height: calc(100% - #{px_to_rem(6)} * 2);
          margin: px_to_rem(6) px_to_rem(21);
          span {
            display: block;
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100%; } } } }
      li:nth-child(2) {
        margin: 0 px_to_rem(15); } } } }
